import React from 'react'
import PropTypes from 'prop-types'
import { Navbar, Nav, NavItem } from 'react-bootstrap'
import { LinkContainer } from 'react-router-bootstrap'
import { SMALL_LOGO, ADMIN_USER } from '../../constants'
import { styles } from '../../styles'

export const CakesNav = ({userName}) => (
  <Navbar>
    <Navbar.Header>
      <Navbar.Brand>
        <img style={styles.smallLogo} href='#' src={SMALL_LOGO}/>
      </Navbar.Brand>
      <Navbar.Toggle />
    </Navbar.Header>
    <Navbar.Collapse>
      <Nav>
        <LinkContainer key='shop_link' to='/shop'>
          <NavItem eventKey={1}>Shop</NavItem>
        </LinkContainer>
        <LinkContainer key='about_link' to='/about_us'>
          <NavItem eventKey={2}>About Us</NavItem>
        </LinkContainer>
      </Nav>
      <Nav pullRight>
        {userName === ADMIN_USER &&
        <LinkContainer key='admin_link' to='/admin'>
          <NavItem style={styles.adminLink} eventKey={3} href='#'>Admin</NavItem>
        </LinkContainer>}
        {userName
          ? [<LinkContainer key='sign_in' to='/sign_in'>
            <NavItem pullRight eventKey={4}>Sign In</NavItem>
          </LinkContainer>,
            <LinkContainer key='sign_up' to='/sign_up'>
              <NavItem pullRight eventKey={4}>Sign Up</NavItem>
            </LinkContainer>]
          : [<LinkContainer key='profile' to={`/profile/${userName}`}>
            <NavItem pullRight eventKey={4}>Profile, {userName}</NavItem>
          </LinkContainer>,
            <LinkContainer key='sign_out' to='/sign_out'>
              <NavItem pullRight eventKey={4}>Sign Out</NavItem>
            </LinkContainer>]
        }
      </Nav>
    </Navbar.Collapse>
  </Navbar>
)

CakesNav.propTypes = {
  userName: PropTypes.string
}

export default CakesNav



