export const styles = {
  rootContainer: {
    display: 'flex',
    flexDirection: 'column'
  },
  body: {
    background: '#e8effd',
    fontFamily: 'Roboto',
    margin: 0
  },
  smallLogo: {
    objectFit: 'cover',
    maxHeight: '42px',
    maxWidth: '196px'
  },
  adminLink: {
    color: 'red'
  },
  header: {
    bigLogo: {
      objectFit: 'cover',
      maxHeight: '252px',
      maxWidth: '574px'
    },
    divLogoWrapper: {
      widths: '100%',
      alignContent: 'center',
      height: '252px',
      display: 'block',
      '@media screen and (min-height: 700px)': {
        display: 'none'
      }
    }
  }
}



